import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import React, { Suspense, lazy, useEffect } from "react";
import { useDispatch } from "react-redux";
import "./App.css";
import AppHeader from "./components/AppHeader.jsx";
import FoliaLoader from "./components/FoliaLoader.jsx";
import { MDBContainer } from "mdbreact";
import {
  makeURL,
  API_CAMAROTES_CATEGORY,
  API_BLOCOS_CATEGORY
} from "./urls/urls";

const Home = lazy(() => import("./components/home/Home"));
const CompreAqui = lazy(() => import("./components/compreaqui/CompreAqui"));
const Camarotes = lazy(() => import("./components/camarotes/Camarotes"));
const Blocos = lazy(() => import("./components/blocos/Blocos"));
const Navios = lazy(() => import("./components/navios/Navios"));
const Hoteis = lazy(() => import("./components/hoteis/Hoteis"));
const Atendimento = lazy(() => import("./components/atendimento/Atendimento"));
const MinhaConta = lazy(() => import("./components/minhaconta/MinhaConta"));

const FETCH_CAMAROTES = "FETCH_CAMAROTES";
const FETCH_BLOCOS = "FETCH_BLOCOS";

function fetchCamarotesAction(products) {
  return { type: FETCH_CAMAROTES, products };
}

function fetchBlocosAction(products) {
  return { type: FETCH_BLOCOS, products };
}

function App() {
  const containerStyle = { maxWidth: "600px" };
  const dispatch = useDispatch();

  function fetchUrl(url, callback) {
    fetch(url) //todos os produtos
      .then(res => res.json())
      .then(res => {
        if (res.error) {
          throw res.error;
        }
        dispatch(callback(res));
      })
      .catch(error => {
        //dispatch(fetchBlocosError(error));
      });
  }

  useEffect(() => {
    fetchUrl(makeURL(API_CAMAROTES_CATEGORY), fetchCamarotesAction);
    fetchUrl(makeURL(API_BLOCOS_CATEGORY), fetchBlocosAction);
  });

  return (
    <div className="App">
      <Router basename="/foliabahiamobile">
        <MDBContainer style={containerStyle} className="mb-2">
          <AppHeader />
          <Suspense fallback={<FoliaLoader />}>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/compre-aqui" component={CompreAqui} />
              <Route path="/camarotes" component={Camarotes} />
              <Route path="/blocos" component={Blocos} />
              <Route path="/navios" component={Navios} />
              <Route path="/hoteis" component={Hoteis} />
              <Route path="/atendimento" component={Atendimento} />
              <Route path="/minha-conta" component={MinhaConta} />
            </Switch>
          </Suspense>
        </MDBContainer>
      </Router>
    </div>
  );
}

export default App;
