import { createStore } from "redux";

const FETCH_CAMAROTES = "FETCH_CAMAROTES";
const FETCH_BLOCOS = "FETCH_BLOCOS";

const INITIAL_STATE = {
  blocos: [],
  camarotes: []
};

function productsReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case FETCH_CAMAROTES:
      return { ...state, camarotes: action.products };
    case FETCH_BLOCOS:
      return { ...state, blocos: action.products };
    default:
      return state;
  }
}

const store = createStore(productsReducer);

export default store;
